'use client';

import { useEffect, useState } from 'react';
import { collection, getDocs, query, where } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { SimpleSelect } from '@/src/components/ui/select';

interface BatterySelectorProps {
  value: string;
  onChange: (val: string) => void;
}

export default function BatterySelector({ value, onChange }: BatterySelectorProps) {
  const [batteryOptions, setBatteryOptions] = useState<{ label: string; value: string }[]>([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchBatteries = async () => {
      try {
        const q = query(collection(db, 'batteries'), where('status', '==', 'in_stock'));
        const snap = await getDocs(q);
        const options = snap.docs
          .map((doc) => doc.data()?.batteryCode as string)
          .filter(Boolean)
          .map((code) => ({
            label: code,
            value: code,
          }));
        setBatteryOptions(options);
      } catch (err) {
        console.error('‚ùå Error loading batteries:', err);
      } finally {
        setLoading(false);
      }
    };

    fetchBatteries();
  }, []);

  return (
    <SimpleSelect
      value={value}
      onChange={onChange}
      options={batteryOptions}
      placeholder={loading ? 'üîÑ Loading battery codes...' : 'Select battery code'}
    />
  );
}
