'use client';

import { useEffect, useState } from 'react';
import { collection, getDocs, query, where } from 'firebase/firestore';
import { db } from '@/src/firebaseConfig';
import { SubscriptionPackage } from '@/src/lib/subscriptionPackages/subscriptionPackagesType';
import { SimpleSelect } from '@/src/components/ui/select';

interface PackageSelectorProps {
  companyId: string;
  value: string;
  onChange: (value: string) => void;
}

export default function PackageSelector({
  companyId,
  value,
  onChange,
}: PackageSelectorProps) {
  const [packages, setPackages] = useState<SubscriptionPackage[]>([]);
  const [loading, setLoading] = useState(false);

  useEffect(() => {
    const fetchPackages = async () => {
      setLoading(true);

      try {
        const q = query(
          collection(db, 'subscriptionPackages'),
          where('companyId', '==', companyId)
        );
        const snap = await getDocs(q);
        const data = snap.docs.map((doc) => ({
          id: doc.id,
          ...(doc.data() as SubscriptionPackage),
        }));
        setPackages(data);
      } catch (error) {
        console.error('‚ùå Error fetching packages:', error);
      } finally {
        setLoading(false);
      }
    };

    if (companyId && typeof companyId === 'string') {
      fetchPackages();
    } else {
      console.warn('‚ö†Ô∏è companyId is invalid:', companyId);
    }
  }, [companyId]);

  const options = packages.map((pkg) => ({
    label: pkg.name,
    value: pkg.id ?? '',
  }));

  return (
    <SimpleSelect
      value={value}
      onChange={onChange}
      options={options}
      placeholder={loading ? 'üîÑ Loading packages...' : '-- Select a package --'}
    />
  );
}
